const axios = require('axios');
export class ApiService {
    constructor() {
        axios.defaults.baseURL = 'https://604b401bee7cb900176a1a9e.mockapi.io/api/v1/';
    }
    get(url) {
        axios.get(url)
        .then((res)=> {
            console.log(res.data);
            return(
                res && res.data
            )
        })
        .catch((error) => {
            console.log('Error :', error);
        })
    }
}
